<template>
    <div class="login-wapper">
        <div class="bg-wapper">
            <text class="title"></text>
            <div class="input-wapper">
                <text class="icon">{{userIcon}}</text>
                <input v-model="params.phone" return-key-type="defalut"
                       autocomplete="off"
                       placeholder=""
                       class="input"
                />
            </div>
            <div class="input-wapper">
                <text class="icon">{{passIcon}}</text>
                <input v-model="params.password" return-key-type="go"
                       autocomplete="off"
                       type="password"
                       placeholder=""
                       class="input"
                />
            </div>
            <text class="button" @click="login">   </text>
            <div class="more">
                <text class="go-register" @click="tip"></text>
                <router-link to="/home">
                    <text class="go-home"></text>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    import Api from '@/apis/login/api'
    const modal = weex.requireModule('modal')
    export default {
        name: "login",
        data(){
            return{
                userIcon : '\uf007',
                passIcon : '\uf023',
                params:{
                    phone:'',
                    password:''
                }
            }
        },
        created(){
            Api.setVue(this);
        },
        methods:{
            tip : function(){
                modal.toast({ message:'',duration:3})
            },
            login:function(){
                if(this.params.phone==''||this.params.password==''){
                    modal.toast({
                        message:'',
                        duration:3
                    })
                }else{
                    Api.login(this.params).then(d=>{
                        if(d.code==0){
                            this.$store.setToken(d.data.token)
                            this.$router.push("/home")
                        }else{
                            modal.toast({ message:'',duration:3})
                        }
                    }).catch(e=>{
                        console.log(e)
                    })
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    @import '../../styles/common';
    .login-wapper {
        width: 750px;
        flex: 1;
        justify-content: center;
        align-items: center;
        background-image: linear-gradient(to top, transparent, #fb405f);
    }
    .bg-wapper{
        width: 750px;
        flex: 1;
        justify-content: center;
        align-items: center;
        background-image: linear-gradient(to top left, #59c3f8, transparent);
    }
    .title{
        font-size: 52px;
        color: @bg-white;
        margin: 35px 0px;
    }
    .icon{
        font-size: 32px;
    }
    .input-wapper {
        flex-direction: row;
        width: 700px;
        border-bottom-width: 1px;
        border-bottom-color: @bg-white;
        padding: 15px 0px;
        align-items: center;
        margin: 15px 0px;
    }
    .more{
        margin-top: 25px;
        flex-direction: row;
    }
    .go-register{
        font-size: 24px;
        color: #fff;
        text-decoration: underline;
        margin-right: 35px;
    }
    .go-home{
        font-size: 24px;
        color: #fff;
        text-decoration: underline;
    }
    .input{
        border: none;
        flex: 1;
        line-height: 30px;
        font-size: 28px;
        color: @bg-white;
        background-color: transparent;
        margin-left: 20px;
        placeholder-color:#fff;
    }
    .input :active,.input :hover{
        background-color: transparent;
    }
    .button{
        margin-top:60px;
        background-color: #fb405f;
        width: 260px;
        height: 70px;
        border-radius: 10px;
        color: @bg-white;
        font-size: 32px;
        text-align: center;
        line-height: 70px;
    }
</style>



